.nav-item {
    margin: 0;
    box-sizing: border-box;
    display: flex;
    height: 100%;
    align-items: center;
}

.nav-item a{
    color: white;
    text-decoration: none;
    height: 100%;
    padding: 16px 10px;
    box-sizing: border-box;
    display: block;
}

.nav-item a:hover{
    background: #e1bee7;
    color: #8e24aa;
    cursor: pointer;
}