.modal{
    position: fixed;
    z-index: 300;
    background: #ffffffff;
    width: 25%;
    border:1px solid #e0e0e0e0;
    border-radius: 5px;
    padding: 16px;
    left: 38%;
    top: 25%;
    box-sizing: border-box;
    text-align: center;
    transition: all 0.3s ease-out;
}

@media (max-width: 60rem){
    .modal{
        width:30%;
        left:35%;
    }
}